/* css reset */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #dfe3e9 !important;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

.list-head {
  font-size: 1.4rem;
  color: grey;
  padding: 5px 0;
}

.wrapper {
  width: 100%;
  justify-content: center;
  padding: 2rem;
}

.droppable {
  background-color: #dfe3e9;
  margin: 0 0 2rem 2rem;
  box-shadow: 1px 1px 1px 2px #ccc;
  border-top-style: solid;
  border-top-color: coral;
  max-width: 30% !important ;
}

.list-item {
  background-color: #fdfdfd;
  margin: 20px 0;
  padding: 5px 10px;
  min-height: 100px;
  text-align: center;
  overflow-wrap: break-word;
  word-break: break-all;
}

.hover {
  background-color: rgba(9, 45, 66, 0.08);
}

.edit-button {
  padding: 1px 6px !important;
}

.btn-danger {
  margin: -1px 10px;
}
.add-new-list {
  margin: 1rem 1rem;
}
.add-new-item {
  margin: 1rem 0;
}

.add-list-input {
  display: inline-block !important;
  max-width: 400px;
  margin: 0 10px 5px 0;
  height: 45px !important;
}

.btn {
  margin-top: 5px;
}

.move-down {
  margin-top: 100px;
}

.main-head {
  margin: 1rem;
}

.workspace {
  margin: 1rem;
  /* padding: 0.5rem; */
}

.add-list {
  margin-left: 0.5rem;
  padding: 0.5rem;
  color: hsla(0, 0%, 100%, 0.8);
  transition: color 85ms ease-in;
  background-color: rgba(0, 0, 0, 0.24);
  cursor: pointer;
}

.add-list-wrapper {
  margin-top: 1rem;
  background-color: grey;
  border-radius: 3px;
  height: auto;
  padding: 4px;
  width: 15rem;
}

.list-name-input {
  width: 100%;
  padding: 10px;
  height: 2rem;
  background: #fff;
  border: none;
  box-shadow: inset 0 0 0 2px #0079bf;
  display: block;
  margin: 0;
}

.show {
  display: block;
}
.hide {
  visibility: hidden;
}

